<template>
    <div class="shopPage">
        
        <div :class="isDeleted.length == cant_items? 'no-items-found-active' : 'no-items-found-no-active' " >
            <img class="ficon no-items" src="../assets/icons/shopcar.svg" alt="shopcar icon">
            <p class="description-no-items">No se encontraron items</p>
        </div>


        <div :class=" isDeleted.length != cant_items || polos_items.length ? 'container-shop' : 'no-items-found-no-active' "> 

            <div class="details-cost">
                <div>
                    <div class="items" >

                        <div class="card" v-for="product in polos_items" v-bind:key="product.id">

                            <div class="item-div" :class="isDeleted.indexOf(product.id) == -1 ? 'item-div-active' : 'item-div-deleted'">
                                
                                <a class="item-content">
                                    <button class="btn btn-danger btn-sd" @click="deleteShopItem(product.id)">X</button>

                                    <a  :href="'/items/'+product.id">
                                        <figure class="figure-item">
                                            <img class="image-item" v-bind:src="product.get_image" alt="">
                                        </figure>
                                    </a>

                                    <div class="info">
                                        <h4>{{product.modelo}}</h4>
                                        <p class="info-data"><span class=bold>Marca: </span> {{product.marca}}</p>
                                        <p class="info-data"><span class=bold>Talla: </span> {{product.talla}}</p>
                                        <p class="info-data"><span class=bold>Precio: </span> S/. {{product.precio}}</p>
                                        <p class="info-data"><span class=bold>Cantidad: </span> {{product.cantidad}}</p>
                                    </div>
                                    
                            
                                </a>
                            
                            </div> 
                        
                        </div> 
                    </div>
                </div>
            </div>

            <div class="total-cost">
                <div class="card-total">
                    <h1 class="bold">Total</h1>
                    <h2>S/. {{precio_total}}</h2>
                    <button type="button" class="btn-compra btn btn-dark" @click="toPay">Continuar con la compra</button>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
import Swal from 'sweetalert2';

export default{
    name: "MyShopCar",
    data(){
        return {
            polos_items: [],
            precio_total: 0,
            isDeleted: [],
            cant_items: 0,
            sendDataShop: {
                "list_id_polos": [],
                "list_count": [],
                "id_user": null
            },
            usr_id: null,
        }
    },
    mounted(){
        this.checkUserSession();
        this.getShopList();
    },
    methods:{
        getShopList(){
            var items_shop = localStorage.getItem("startCompras");
            items_shop = JSON.parse(items_shop);
            console.log(items_shop);
            if(items_shop != null){
                this.polos_items = items_shop["results"];
                this.cant_items = this.polos_items.length;
                console.log("cant: ",this.cant_items);

                this.polos_items.forEach( e => {
                    
                    this.sendDataShop["list_id_polos"].push(e.id);
                    this.sendDataShop["list_count"].push((e.cantidad).toString());

                    this.precio_total += parseFloat(e.precio) * parseInt(e.cantidad);
                    this.precio_total = Math.round(this.precio_total * 100) / 100;
                });
                //console.log("isDeleted: ",this.isDeleted.length);
            }

        },
        deleteShopItem(id){
            console.log(id);
            this.isDeleted.push(id);

            var items_shop = JSON.parse(localStorage.getItem("startCompras"));
            var new_items = items_shop["results"];

            for(var i=0;i<new_items.length;i++){
                if(new_items[i].id === id){
                    this.precio_total -= new_items[i].precio;
                    this.precio_total = Math.round(this.precio_total * 100) / 100;
                    new_items.splice(i,1);
                }
            }

            console.log("res:",new_items);
            console.log(new_items.length);

            this.polos_items = new_items;
            const compras = {"results" : []};
            compras["results"] = new_items;

            console.log("len:",this.polos_items.length);

            if(this.isDeleted.length == this.cant_items) localStorage.removeItem("startCompras");
            else{
                localStorage.setItem("startCompras",JSON.stringify(compras))
            }
        },

        toPay(){
            this.sendDataShop["id_user"] = this.usr_id;

            console.log("dataShop: ",this.sendDataShop);
            
            axios
                .post('/api/carritoCompras/sale/',this.sendDataShop)
                .then(response =>{
                    console.log("enviado: ",response.data.msj);    
                })
                .catch(error => {
                    console.log("pipippipi");
                    console.log(error);
                })

            Swal.fire('Compra exitosa!', 'Gracias por probar el demo','success');
            localStorage.removeItem("startCompras");
            console.log("pague")
        },
        checkUserSession(){
            const activeUserSession = localStorage.getItem("user_session");
            if(activeUserSession != null){
                this.usr_id = activeUserSession;
            }
        },
        
    },
    created() {
        //console.log("aewa: ", this.$router.currentRoute._value.fullPath); // path is /post
    },

}
</script>

<style scoped>

.shopPage{
    padding: 70px 0;
    background-color: #F2F5F9;
}


.container-shop{
    display:flex;
    justify-content: center;
    gap: 5%;
    padding:50px 0; 
}   

/*Start Detalles costos*/

.details-cost{
    padding: 5px 50px;
}

.card{
    width: 460px;
    border: none;
    margin-top: 20px;
    border-radius: 30px;
    background: #FFE3DE;
    box-shadow: 15px 18px 16px -3px rgba(0,0,0,0.1);
}

.item-div-deleted{
    display: none;
}

.item-div-active{
    display: block;
}


.item-content{
    display: flex;
    text-decoration: none;
}


.figure-item{
    margin-left: 15px;
    margin-bottom: 5px;
    width: 150px;
    height: 170px;
}

.image-item{
    width: 135px;
    height: 150px;
    margin-top: 10px;
    margin-bottom: 0;
}

.info{
    padding: 10px 5px;
    padding-left: 15px;
    color: black;
}

.info-data{
    padding: 0;
    margin: 0;
}

.bold{
    font-weight: 700;
}


.btn{
    margin-top: 15px;
    height: 10%;
    margin-left: 10px;
}

/*End Detalles costos*/

/*Start Costo total*/

.total-cost{
    text-align: center;
    height: 500px;
    border: 5px solid #0A043C;
    background-color: #A3DDCB;
    margin-right: 100px;
    width: 100% !important;
    padding: 0 20px;
}

.card-total{
    width: 100%;
    padding-top: 50%;
}

.btn-compra{
    margin: auto;
    margin-top: 50px;
}

/*End Costo total*/


.ficon{
    width: 20px;
    margin-left: 800px;
    filter: brightness(0) saturate(100%) invert(18%) sepia(86%) saturate(5836%) hue-rotate(335deg) brightness(93%) contrast(95%);
}

.no-items{
    width: 200px;
    display: block;
    margin: auto;
    margin-top: 60px;
}

.description-no-items{
    text-align: center;
    margin-top: 20px;
    padding-bottom: 200px;
}

.no-items-found-active{
    display: block;
    padding-top: 100px;
}

.no-items-found-no-active{
    display: none;
}

@media only screen and (max-width: 520px){
    .card{
        width: 90vw ;
    }

    .details-cost{
        padding: 5px 0;
    }

    .container-shop{
        flex-direction: column;
        align-items: center;
    }

    .total-cost{
        margin: auto;
        margin-top: 100px;
        height: 250px;
        width: 70% !important;
        padding: 0 20px;
    }

    .card-total{
        width: 100%;
        padding-top: 20px;
    }

}


@media only screen and (min-width: 521px){
    .container-shop{
        flex-direction: column;
        align-items: center;
    }

    .total-cost{
        margin: auto;
        margin-top: 100px;
        height: 250px;
        width: 70% !important;
        padding: 0 20px;
    }

    .card-total{
        width: 100%;
        padding-top: 20px;
    }

}


</style>